import React, { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { Theme } from "common/constants/enum";
import { Heading, HeadingTag } from "common/components/new/Heading";
import { HorizontalScroll } from "common/components/HorizontalScroll";
import { Icon } from "common/components/Icons";
import TrustpilotIcon from "common/components/Icons/icons/trustpilot-full.svg";
import { Link } from "common/components/Link";

import { Review, ReviewProps } from "./components/Review";
import { OurResultsProps } from "./OurResults.types";
import styles from "./OurResults.module.scss";

const cx = classNames.bind(styles);

export const OurResults: FunctionComponent<OurResultsProps> = (props) => {
    const {
        title,
        reviews,
        className,
        classNameTitle,
        link,
        theme = Theme.DARK,
    } = props;
    const renderReviews = (review: ReviewProps, index: number) => {
        return (
            <Review
                {...review}
                key={index}
                className={cx("review")}
                theme={theme}
            />
        );
    };
    const reviewsComponent = (
        <div className={cx("content")}>{reviews.map(renderReviews)}</div>
    );

    const renderIcon = (iconName?: string) => {
        switch (iconName) {
            case "trustpilotFull":
                return <TrustpilotIcon />;
            default:
                return null;
        }
    };

    return (
        <div
            className={cx(className, "root", {
                root_light: theme === Theme.LIGHT,
            })}
        >
            <Heading
                className={cx(classNameTitle, "title")}
                tag={HeadingTag.H2}
            >
                <span dangerouslySetInnerHTML={{ __html: title }} />
            </Heading>
            <div
                className={cx("mobile", {
                    mobile_with_border: link,
                })}
            >
                <HorizontalScroll>{reviewsComponent}</HorizontalScroll>
            </div>
            <div
                className={cx("desktop", {
                    desktop_with_border: link,
                })}
            >
                {reviewsComponent}
            </div>
            {link && (
                <Link link={link.to} target="_blank">
                    <Icon>{renderIcon(link.icon)}</Icon>
                </Link>
            )}
        </div>
    );
};
