import React, { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { Theme } from "common/constants/enum";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Picture } from "common/components/Picture";
import { Icon } from "common/components/Icons";
import DotIcon from "common/components/Icons/icons/24/dot.svg";

import { Rating } from "../Rating";
import { ReviewProps } from "./Review.types";
import styles from "./Review.module.scss";

const cx = classNames.bind(styles);

export const Review: FunctionComponent<ReviewProps> = (props) => {
    const {
        image,
        name,
        className,
        subtitle,
        treatmentPlan,
        theme = Theme.DARK,
        content,
        img,
        hasTrustpilotRating = true,
    } = props;

    return (
        <div
            className={cx(className, "root", {
                root_light: theme === Theme.LIGHT,
            })}
        >
            {img && (
                <div
                    className={cx("img")}
                    style={{ backgroundImage: `url(${img})` }}
                />
            )}
            {!img && (
                <Picture
                    className={cx("picture")}
                    classNameImg={cx("image")}
                    mobile={image.mobile}
                    desktop={{
                        ...image.desktop,
                        media: "(min-width: 1024px)",
                    }}
                />
            )}
            {name && (
                <Heading
                    className={cx("name")}
                    type={HeadingType.M}
                    tag={HeadingTag.H3}
                >
                    {name}
                </Heading>
            )}
            {subtitle && treatmentPlan && (
                <div className={cx("subtitle-block")}>
                    {subtitle}
                    <div className={cx("ellipse-icon")}>
                        <Icon>
                            <DotIcon />
                        </Icon>
                    </div>
                    {treatmentPlan}
                </div>
            )}
            {content && <div className={cx("content")}>{content}</div>}
            {hasTrustpilotRating && <Rating />}
        </div>
    );
};
